
import { defineComponent, PropType } from "vue";

import { IProject } from "../assets/projects";

export default defineComponent({
    name: "ProjectItem",
    props: {
        project: {
            type: Object as PropType<IProject>,
            required: true,
        },
    },
    data() {
        return {
            isVisible: false,
        };
    },
    methods: {
        visibilityChanged(isVisible: boolean) {
            this.isVisible = isVisible;
        },
    },
});
