
import HelloSvg from "@/svg/Hello.vue";
import { defineComponent } from "@vue/runtime-core";
import VTypical from "vue-typical";

export default defineComponent({
  name: "Header",
  data() {
    return {
      isVisible: false,
    };
  },
  components: {
    HelloSvg,
    VTypical,
  },
  methods: {
    visibilityChanged(isVisible: boolean) {
      this.isVisible = isVisible;
    },
  },
});
